.container_card {
    background-color: white;
    padding: 3px;
    margin: 5px;
    width: 200px;
    height: 259px;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 5px;
}



.container_card img {
    width: auto;
    height: 8.25em;
    margin-bottom: 10px;
}

.container_card h1 {
    font-size: 1em;
    color: #222;
    text-align: center;
    margin-bottom: 5px;
}

.container_card p {
    font-weight: bolder;
    font-size: 1.2em;
    margin-bottom: 10px;
    color: #f58c02;
}
