.button {
    border: none;
    background-color: #f58c02;
    padding: 5px 20px;
    cursor: pointer;
    color: white;
    font-weight: bolder;
    font-size: 1em;
    display: flex;
    align-items: center;
    border-radius: 5px;
    transition: .2s;
}

.button:hover {
    background-color: white;
    color: #f58c02;
    border: 2px solid #f58c02;
}

.button svg {
    font-size: 1.5em;
    margin-right: 7px;
    margin-bottom: 4px;
}