.navbar {
    width: 95%;
    margin: auto;
    margin-top: 5px;
    border-top: 1px solid #222;
}

.ul_list {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px 1px;
}

.item {
    margin-right: 25px;
}

.item a {
    color: black;
    transition: .15s;
}

.item a:hover {
    color: #f58c02;
}